.profile__button-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.4s;
  cursor: pointer;
  opacity: 0;
}

.profile__button-overlay:hover {
  background-color: rgba(0, 0, 0, 0.8);
  opacity: 1;
}
