.profile__title {
  max-width: 295px;
  font-weight: 500;
  font-size: 42px;
  line-height: 56px;
  color: #fff;
  margin: 0;
  padding: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 426px) {
  .profile__title {
    max-width: 200px;
    font-size: 27px;
    text-align: center;
    line-height: 38px;
  }
}
